.footer {
  background: var(--bg-secondary);
  padding: 60px 0 20px;
  border-top: 1px solid var(--border-color);
  position: relative;
}

.footer-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 40px;
  margin-bottom: 40px;
}

.footer-section h3,
.footer-section h4 {
  color: var(--text-primary);
  margin-bottom: 20px;
  font-weight: 700;
}

.footer-logo {
  font-size: 1.75rem;
  background: linear-gradient(135deg, var(--color-accent), #0066ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 16px;
}

.footer-description {
  color: var(--text-secondary);
  line-height: 1.6;
  max-width: 280px;
}

.contact-info {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 12px;
  color: var(--text-secondary);
  text-decoration: none;
  transition: color 0.3s ease;
  padding: 8px 0;
}

.contact-item:hover {
  color: var(--color-accent);
}

.contact-item svg {
  font-size: 16px;
  color: var(--color-accent);
}

.footer-links {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-links li {
  margin-bottom: 12px;
}

.footer-links a {
  color: var(--text-secondary);
  text-decoration: none;
  transition: color 0.3s ease;
  padding: 4px 0;
  display: block;
}

.footer-links a:hover {
  color: var(--color-accent);
}

.social-links {
  display: flex;
  gap: 16px;
  justify-content: flex-start;
}

.social-links a {
  width: 40px;
  height: 40px;
  border-radius: 8px;
  background: var(--bg-primary);
  border: 1px solid var(--border-color);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-secondary);
  text-decoration: none;
  transition: all 0.3s ease;
}

.social-links a:hover {
  background: linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%);
  border-color: transparent;
  color: white;
  transform: translateY(-2px);
}

.footer-bottom {
  padding-top: 20px;
  border-top: 1px solid var(--border-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
}

.footer-legal {
  display: flex;
  gap: 24px;
}

.footer-legal a {
  color: var(--text-secondary);
  text-decoration: none;
  font-size: 14px;
  transition: color 0.3s ease;
}

.footer-legal a:hover {
  color: var(--color-accent);
}

.footer-copyright {
  color: var(--text-secondary);
  margin: 0;
  font-size: 14px;
}

@media (max-width: 768px) {
  .footer {
    padding: 50px 0 20px;
  }
  
  .footer-content {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 32px;
    margin-bottom: 32px;
  }
  
  .footer-section h4 {
    font-size: 1.1rem;
    margin-bottom: 16px;
  }
  
  .footer-bottom {
    flex-direction: column;
    text-align: center;
    gap: 16px;
  }
  
  .footer-legal {
    gap: 20px;
  }
}

@media (max-width: 480px) {
  .footer {
    padding: 40px 0 20px;
  }
  
  .footer-content {
    grid-template-columns: 1fr;
    gap: 28px;
    text-align: center;
  }
  
  .footer-description {
    max-width: none;
  }
  
  .contact-info {
    align-items: center;
  }
  
  .social-links {
    justify-content: center;
  }
  
  .footer-legal {
    flex-direction: column;
    gap: 12px;
  }
}