.admin-demo {
  background: var(--bg-primary);
  position: relative;
  overflow: hidden;
}

.admin-demo::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(0, 76, 191, 0.02) 0%, transparent 50%);
  z-index: 0;
}

.demo-header {
  text-align: center;
  max-width: 700px;
  margin: 0 auto 80px;
  position: relative;
  z-index: 1;
}

.section-title {
  font-size: clamp(2.5rem, 5vw, 3.5rem);
  font-weight: 800;
  color: var(--text-primary);
  margin-bottom: 20px;
  letter-spacing: -0.025em;
  line-height: 1.1;
}

.gradient-text {
  background: linear-gradient(135deg, var(--color-accent), #0066ff, #00a3ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.section-description {
  font-size: 20px;
  color: var(--text-secondary);
  line-height: 1.6;
  max-width: 600px;
  margin: 0 auto;
}

.video-container {
  max-width: 900px;
  margin: 0 auto 80px;
  position: relative;
  z-index: 1;
}

.video-wrapper {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%; /* 16:9 aspect ratio */
  border-radius: 16px;
  overflow: hidden;
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.1);
}

[data-theme="dark"] .video-wrapper {
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
}

.video-wrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.video-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(0, 76, 191, 0.9) 0%, rgba(0, 102, 255, 0.8) 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
  border-radius: 16px;
}

.play-button-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
}

.play-button {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.95);
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  color: var(--color-accent);
  cursor: pointer;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  margin-left: 4px; /* Adjust for play icon optical alignment */
}

.play-button:hover {
  background: rgba(255, 255, 255, 1);
  box-shadow: 0 12px 48px rgba(0, 0, 0, 0.3);
}

.video-preview {
  position: absolute;
  bottom: 32px;
  left: 32px;
  right: 32px;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(20px);
  border-radius: 12px;
  padding: 24px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.preview-content h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: white;
  margin: 0 0 8px 0;
  line-height: 1.2;
}

.preview-content p {
  font-size: 16px;
  color: rgba(255, 255, 255, 0.8);
  margin: 0 0 16px 0;
  line-height: 1.4;
}

.video-duration {
  display: inline-block;
  background: rgba(255, 255, 255, 0.2);
  color: white;
  padding: 4px 12px;
  border-radius: 16px;
  font-size: 14px;
  font-weight: 600;
}

.video-controls {
  position: absolute;
  top: 16px;
  right: 16px;
  display: flex;
  gap: 8px;
  z-index: 10;
}

.control-button {
  width: 40px;
  height: 40px;
  border-radius: 8px;
  background: rgba(0, 0, 0, 0.6);
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.control-button:hover {
  background: rgba(0, 0, 0, 0.8);
  transform: scale(1.05);
}

.demo-features {
  margin-bottom: 80px;
  position: relative;
  z-index: 1;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 32px;
  max-width: 1100px;
  margin: 0 auto;
}

.feature-item {
  text-align: center;
  padding: 36px 28px;
  background: var(--bg-primary);
  border: 1px solid var(--border-color);
  border-radius: 20px;
  transition: all 0.3s ease;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.06);
  position: relative;
  overflow: hidden;
}

.feature-item:hover {
  border-color: var(--color-accent);
  box-shadow: 0 12px 40px rgba(0, 76, 191, 0.15);
  transform: translateY(-6px);
}

[data-theme="dark"] .feature-item {
  background: var(--bg-secondary);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.3);
}

.feature-icon {
  font-size: 3rem;
  margin-bottom: 20px;
  line-height: 1;
  filter: drop-shadow(0 2px 4px rgba(0, 76, 191, 0.1));
}

.feature-item h4 {
  font-size: 1.4rem;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0 0 16px 0;
  line-height: 1.3;
}

.feature-item p {
  font-size: 16px;
  color: var(--text-secondary);
  line-height: 1.6;
  margin: 0;
  opacity: 0.9;
}

.demo-cta {
  text-align: center;
  max-width: 600px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

.demo-cta h3 {
  font-size: 2rem;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0 0 16px 0;
  line-height: 1.2;
}

.demo-cta p {
  font-size: 18px;
  color: var(--text-secondary);
  margin: 0 0 32px 0;
  line-height: 1.5;
}

.demo-cta .btn {
  padding: 18px 36px;
  font-size: 18px;
  font-weight: 600;
  border-radius: 16px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .demo-header {
    margin-bottom: 60px;
  }
  
  .section-title {
    font-size: 2.5rem;
    margin-bottom: 16px;
  }
  
  .section-description {
    font-size: 18px;
  }
  
  .video-container {
    margin-bottom: 60px;
  }
  
  .play-button {
    width: 64px;
    height: 64px;
    font-size: 20px;
  }
  
  .video-preview {
    bottom: 20px;
    left: 20px;
    right: 20px;
    padding: 20px;
  }
  
  .preview-content h3 {
    font-size: 1.25rem;
  }
  
  .preview-content p {
    font-size: 14px;
  }
  
  .features-grid {
    grid-template-columns: 1fr;
    gap: 24px;
  }
  
  .feature-item {
    padding: 28px 20px;
  }
  
  .feature-icon {
    font-size: 2rem;
  }
  
  .demo-features {
    margin-bottom: 60px;
  }
  
  .demo-cta h3 {
    font-size: 1.75rem;
  }
  
  .demo-cta p {
    font-size: 16px;
  }
  
  .demo-cta .btn {
    padding: 16px 32px;
    font-size: 16px;
  }
}

@media (max-width: 480px) {
  .section-title {
    font-size: 2rem;
  }
  
  .section-description {
    font-size: 16px;
  }
  
  .play-button {
    width: 56px;
    height: 56px;
    font-size: 18px;
  }
  
  .video-preview {
    bottom: 16px;
    left: 16px;
    right: 16px;
    padding: 16px;
  }
  
  .preview-content h3 {
    font-size: 1.1rem;
  }
  
  .features-grid {
    gap: 20px;
  }
  
  .feature-item {
    padding: 24px 16px;
  }
  
  .feature-icon {
    font-size: 1.75rem;
    margin-bottom: 12px;
  }
  
  .feature-item h4 {
    font-size: 1.1rem;
  }
  
  .feature-item p {
    font-size: 14px;
  }
  
  .demo-cta h3 {
    font-size: 1.5rem;
  }
  
  .control-button {
    width: 36px;
    height: 36px;
  }
}

