.client-showcase {
  background: linear-gradient(135deg, var(--bg-primary) 0%, var(--bg-secondary) 100%);
  position: relative;
  overflow: hidden;
}

.client-showcase::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(ellipse at center, rgba(0, 76, 191, 0.03) 0%, transparent 70%);
  z-index: 0;
}

.showcase-header {
  text-align: center;
  max-width: 700px;
  margin: 0 auto 80px;
  position: relative;
  z-index: 1;
}

.section-title {
  font-size: clamp(2.5rem, 5vw, 3.5rem);
  font-weight: 800;
  color: var(--text-primary);
  margin-bottom: 20px;
  letter-spacing: -0.025em;
  line-height: 1.1;
}

.gradient-text {
  background: linear-gradient(135deg, var(--color-accent), #0066ff, #00a3ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.section-description {
  font-size: 20px;
  color: var(--text-secondary);
  line-height: 1.6;
  max-width: 600px;
  margin: 0 auto;
}

.clients-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 30px;
  margin-bottom: 60px;
  position: relative;
  z-index: 1;
}

.client-card {
  background: var(--bg-primary);
  border: 1px solid var(--border-color);
  border-radius: 20px;
  padding: 28px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.client-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, var(--color-accent), #0066ff);
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: -1;
}

.client-card:hover::before {
  opacity: 0.02;
}

.client-card:hover {
  border-color: var(--color-accent);
  transform: translateY(-8px);
}

[data-theme="dark"] .client-card {
  background: var(--bg-secondary);
}

[data-theme="dark"] .client-card:hover::before {
  opacity: 0.05;
}

.client-header {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 20px;
}

.client-favicon {
  width: 48px;
  height: 48px;
  border-radius: 12px;
  overflow: hidden;
  border: 1px solid var(--border-color);
  background: var(--bg-secondary);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.client-favicon img {
  width: 32px;
  height: 32px;
  object-fit: cover;
}

.favicon-fallback {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  font-weight: 700;
  color: var(--color-accent);
  background: linear-gradient(135deg, var(--color-accent), #0066ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.client-info {
  flex: 1;
}

.client-name {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0 0 4px 0;
  line-height: 1.2;
}

.client-category {
  font-size: 14px;
  color: var(--text-secondary);
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.client-link {
  width: 36px;
  height: 36px;
  border-radius: 8px;
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-secondary);
  text-decoration: none;
  transition: all 0.3s ease;
  flex-shrink: 0;
}

.client-link:hover {
  background: var(--color-accent);
  border-color: var(--color-accent);
  color: white;
  transform: scale(1.1);
}

.client-description {
  font-size: 16px;
  color: var(--text-secondary);
  line-height: 1.5;
  margin: 0 0 20px 0;
}

.client-url {
  margin-bottom: 24px;
}

.client-url span {
  font-size: 14px;
  color: var(--color-accent);
  font-weight: 600;
  background: rgba(0, 76, 191, 0.1);
  padding: 6px 12px;
  border-radius: 20px;
  border: 1px solid rgba(0, 76, 191, 0.2);
}

.client-preview {
  height: 120px;
  background: var(--bg-secondary);
  border-radius: 12px;
  border: 1px solid var(--border-color);
  padding: 16px;
  overflow: hidden;
}

.preview-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.preview-header {
  height: 20px;
  background: linear-gradient(90deg, var(--color-accent), #0066ff);
  border-radius: 4px;
  width: 60%;
}

.preview-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.preview-line {
  height: 12px;
  background: var(--border-color);
  border-radius: 6px;
  width: 100%;
}

.preview-line.short {
  width: 70%;
}

.preview-button {
  height: 16px;
  background: var(--color-accent);
  border-radius: 8px;
  width: 40%;
  margin-top: auto;
}

.showcase-cta {
  text-align: center;
  position: relative;
  z-index: 1;
}

.showcase-cta p {
  font-size: 18px;
  color: var(--text-secondary);
  margin-bottom: 24px;
  font-weight: 500;
}

.showcase-cta .btn {
  padding: 16px 32px;
  font-size: 16px;
  font-weight: 600;
  border-radius: 12px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .showcase-header {
    margin-bottom: 60px;
  }
  
  .section-title {
    font-size: 2.5rem;
    margin-bottom: 16px;
  }
  
  .section-description {
    font-size: 18px;
  }
  
  .clients-grid {
    grid-template-columns: 1fr;
    gap: 24px;
    margin-bottom: 50px;
  }
  
  .client-card {
    padding: 24px;
  }
  
  .client-header {
    gap: 12px;
    margin-bottom: 16px;
  }
  
  .client-favicon {
    width: 40px;
    height: 40px;
  }
  
  .client-favicon img {
    width: 28px;
    height: 28px;
  }
  
  .client-name {
    font-size: 1.1rem;
  }
  
  .client-category {
    font-size: 12px;
  }
  
  .client-link {
    width: 32px;
    height: 32px;
  }
  
  .client-description {
    font-size: 15px;
  }
  
  .client-preview {
    height: 100px;
    padding: 12px;
  }
}

@media (max-width: 480px) {
  .clients-grid {
    gap: 20px;
  }
  
  .client-card {
    padding: 20px;
  }
  
  .section-title {
    font-size: 2rem;
  }
  
  .section-description {
    font-size: 16px;
  }
  
  .client-preview {
    height: 80px;
    padding: 10px;
  }
  
  .preview-header {
    height: 16px;
  }
  
  .preview-line {
    height: 10px;
  }
  
  .preview-button {
    height: 14px;
  }
}


