.image-slider {
  background: linear-gradient(135deg, var(--bg-secondary) 0%, var(--bg-primary) 100%);
  position: relative;
  overflow: hidden;
}

.image-slider::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(ellipse at center, rgba(0, 76, 191, 0.05) 0%, transparent 70%);
  z-index: 0;
}

.slider-header {
  text-align: center;
  max-width: 700px;
  margin: 0 auto 80px;
  position: relative;
  z-index: 1;
}

.section-title {
  font-size: clamp(2.5rem, 5vw, 3.5rem);
  font-weight: 800;
  color: var(--text-primary);
  margin-bottom: 20px;
  letter-spacing: -0.025em;
  line-height: 1.1;
}

.gradient-text {
  background: linear-gradient(135deg, var(--color-accent), #0066ff, #00a3ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.section-description {
  font-size: 20px;
  color: var(--text-secondary);
  line-height: 1.6;
  max-width: 600px;
  margin: 0 auto;
}

.slider-container {
  max-width: 1000px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

.slider-wrapper {
  position: relative;
  height: 500px;
  border-radius: 24px;
  overflow: hidden;
  background: var(--bg-primary);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.1);
  border: 1px solid var(--border-color);
}

[data-theme="dark"] .slider-wrapper {
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
}

.slide {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
}

.slide-image-container {
  flex: 1;
  position: relative;
  overflow: hidden;
}

.slide-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.slide:hover .slide-image {
  transform: scale(1.05);
}

.slide-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, rgba(0, 76, 191, 0.8) 0%, rgba(0, 102, 255, 0.6) 100%);
  display: flex;
  align-items: flex-end;
  padding: 32px;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.slide:hover .slide-overlay {
  opacity: 1;
}

.slide-features {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
}

.feature-tag {
  background: rgba(255, 255, 255, 0.9);
  color: var(--color-accent);
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 600;
  backdrop-filter: blur(10px);
}

.slide-content {
  flex: 0 0 350px;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: var(--bg-primary);
}

.slide-title {
  font-size: 2rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 16px;
  line-height: 1.2;
}

.slide-description {
  font-size: 18px;
  color: var(--text-secondary);
  line-height: 1.6;
  margin: 0;
}

/* Navigation Controls */
.slider-nav {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 50px;
  height: 50px;
  background: rgba(255, 255, 255, 0.9);
  border: none;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  color: var(--color-accent);
  cursor: pointer;
  transition: all 0.3s ease;
  z-index: 10;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

[data-theme="dark"] .slider-nav {
  background: rgba(0, 0, 0, 0.8);
  color: var(--color-white);
}

.slider-nav:hover {
  background: var(--color-accent);
  color: white;
  transform: translateY(-50%) scale(1.1);
}

.slider-nav.prev {
  left: 20px;
}

.slider-nav.next {
  right: 20px;
}

.auto-play-toggle {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 40px;
  height: 40px;
  background: rgba(255, 255, 255, 0.9);
  border: none;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: var(--color-accent);
  cursor: pointer;
  transition: all 0.3s ease;
  z-index: 10;
}

[data-theme="dark"] .auto-play-toggle {
  background: rgba(0, 0, 0, 0.8);
  color: var(--color-white);
}

.auto-play-toggle:hover {
  background: var(--color-accent);
  color: white;
  transform: scale(1.1);
}

/* Dots Indicator */
.slider-dots {
  display: flex;
  justify-content: center;
  gap: 12px;
  margin-top: 30px;
}

.dot {
  width: 12px;
  height: 12px;
  border: none;
  border-radius: 50%;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
}

.dot span {
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: var(--text-tertiary);
  transition: all 0.3s ease;
}

.dot.active span {
  background: var(--color-accent);
  transform: scale(1.2);
}

.dot:hover span {
  background: var(--color-accent);
  transform: scale(1.1);
}

/* Progress Bar */
.progress-bar {
  width: 100%;
  height: 3px;
  background: var(--border-color);
  margin-top: 20px;
  border-radius: 2px;
  overflow: hidden;
}

.progress {
  height: 100%;
  background: linear-gradient(90deg, var(--color-accent), #0066ff);
  border-radius: 2px;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .slider-wrapper {
    height: 450px;
  }
  
  .slide-content {
    flex: 0 0 300px;
    padding: 32px;
  }
  
  .slide-title {
    font-size: 1.8rem;
  }
  
  .slide-description {
    font-size: 16px;
  }
}

@media (max-width: 768px) {
  .slider-header {
    margin-bottom: 60px;
  }
  
  .section-title {
    font-size: 2.5rem;
    margin-bottom: 16px;
  }
  
  .section-description {
    font-size: 18px;
  }
  
  .slider-wrapper {
    height: 400px;
    border-radius: 16px;
  }
  
  .slide {
    flex-direction: column;
  }
  
  .slide-image-container {
    flex: 1;
  }
  
  .slide-content {
    flex: 0 0 auto;
    padding: 24px;
    min-height: 140px;
  }
  
  .slide-title {
    font-size: 1.5rem;
    margin-bottom: 12px;
  }
  
  .slide-description {
    font-size: 15px;
  }
  
  .slider-nav {
    width: 44px;
    height: 44px;
    font-size: 16px;
  }
  
  .slider-nav.prev {
    left: 16px;
  }
  
  .slider-nav.next {
    right: 16px;
  }
  
  .auto-play-toggle {
    top: 16px;
    right: 16px;
    width: 36px;
    height: 36px;
    font-size: 12px;
  }
  
  .slide-overlay {
    padding: 20px;
  }
  
  .feature-tag {
    font-size: 12px;
    padding: 4px 8px;
  }
}

@media (max-width: 480px) {
  .section-title {
    font-size: 2rem;
  }
  
  .section-description {
    font-size: 16px;
  }
  
  .slider-wrapper {
    height: 350px;
    margin: 0 10px;
  }
  
  .slide-content {
    padding: 20px;
    min-height: 120px;
  }
  
  .slide-title {
    font-size: 1.3rem;
  }
  
  .slide-description {
    font-size: 14px;
  }
  
  .slider-nav {
    width: 40px;
    height: 40px;
    font-size: 14px;
  }
  
  .slider-nav.prev {
    left: 12px;
  }
  
  .slider-nav.next {
    right: 12px;
  }
  
  .auto-play-toggle {
    top: 12px;
    right: 12px;
    width: 32px;
    height: 32px;
    font-size: 10px;
  }
  
  .slider-dots {
    gap: 8px;
    margin-top: 20px;
  }
  
  .dot {
    width: 10px;
    height: 10px;
  }
}