.faq {
  background-color: var(--bg-primary);
  text-align: center;
}

.faq h2 {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 40px;
}

.faq-list {
  max-width: 700px;
  margin: 0 auto;
  text-align: left;
}

.faq-item {
  border-bottom: 1px solid var(--border-color);
}

.faq-item:last-child {
  border-bottom: none;
}

.faq-question {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  background: none;
  border: none;
  text-align: left;
  cursor: pointer;
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
}

.faq-question span:last-child {
  font-size: 24px;
  font-weight: 300;
  color: var(--color-accent);
}

.faq-answer {
  padding-bottom: 20px;
}

.faq-answer p {
  color: var(--text-secondary);
  line-height: 1.6;
  margin: 0;
}

@media (max-width: 768px) {
  .faq h2 {
    font-size: 2rem;
    margin-bottom: 32px;
  }
  
  .faq-question {
    font-size: 16px;
    padding: 16px 0;
  }
  
  .faq-answer {
    padding-bottom: 16px;
  }
}