.pricing {
  background: linear-gradient(135deg, var(--bg-primary) 0%, var(--bg-secondary) 100%);
  text-align: center;
  position: relative;
  overflow: hidden;
}

.pricing::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(ellipse at center, rgba(0, 76, 191, 0.03) 0%, transparent 70%);
  z-index: 0;
}

.pricing-header {
  text-align: center;
  max-width: 700px;
  margin: 0 auto 80px;
  position: relative;
  z-index: 1;
}

.section-title {
  font-size: clamp(2.5rem, 5vw, 3.5rem);
  font-weight: 800;
  color: var(--text-primary);
  margin-bottom: 20px;
  letter-spacing: -0.025em;
  line-height: 1.1;
}

.gradient-text {
  background: linear-gradient(135deg, var(--color-accent), #0066ff, #00a3ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.section-description {
  font-size: 20px;
  color: var(--text-secondary);
  line-height: 1.6;
  max-width: 600px;
  margin: 0 auto;
}

.pricing-card {
  max-width: 480px;
  margin: 0 auto;
  background: var(--bg-primary);
  border: 2px solid var(--border-color);
  border-radius: 24px;
  padding: 48px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.08);
  position: relative;
  z-index: 1;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

[data-theme="dark"] .pricing-card {
  background: var(--bg-secondary);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
}

.pricing-badge {
  position: absolute;
  top: -16px;
  left: 50%;
  transform: translateX(-50%);
  background: linear-gradient(135deg, var(--color-accent), #0066ff);
  color: white;
  padding: 8px 20px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 6px;
  box-shadow: 0 8px 20px rgba(0, 76, 191, 0.3);
}

.pricing-header-card {
  margin-bottom: 40px;
}

.price-display {
  display: flex;
  align-items: baseline;
  justify-content: center;
  margin-bottom: 12px;
}

.currency {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-right: 4px;
}

.price {
  font-size: 4rem;
  font-weight: 800;
  color: var(--text-primary);
  line-height: 1;
  letter-spacing: -0.02em;
}

.period {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--text-secondary);
  margin-left: 4px;
}

.billing-info {
  color: var(--text-secondary);
  font-size: 16px;
  margin-bottom: 16px;
}

.savings-badge {
  display: inline-block;
  background: rgba(0, 76, 191, 0.1);
  color: var(--color-accent);
  padding: 6px 16px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 600;
  border: 1px solid rgba(0, 76, 191, 0.2);
}

.features-list {
  margin-bottom: 32px;
  text-align: left;
}

.features-list h4 {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 24px;
  text-align: center;
}

.feature-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 8px 0;
  color: var(--text-primary);
  font-weight: 500;
}

.check-icon {
  color: #10b981;
  font-size: 14px;
  flex-shrink: 0;
}

.guarantee {
  margin-bottom: 32px;
  padding: 16px;
  background: rgba(16, 185, 129, 0.05);
  border: 1px solid rgba(16, 185, 129, 0.2);
  border-radius: 12px;
}

.guarantee p {
  margin: 0;
  color: var(--text-secondary);
  font-size: 14px;
  font-weight: 500;
}

.pricing-cta {
  width: 100%;
  padding: 18px 32px;
  font-size: 18px;
  font-weight: 600;
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  background: linear-gradient(135deg, var(--color-accent), #0066ff);
  border: none;
  color: white;
  box-shadow: 0 8px 32px rgba(0, 76, 191, 0.3);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  margin-bottom: 20px;
}

.no-commitment {
  color: var(--text-secondary);
  font-size: 14px;
  margin: 0;
  font-weight: 500;
}

@media (max-width: 768px) {
  .pricing-header {
    margin-bottom: 60px;
  }
  
  .section-title {
    font-size: 2.5rem;
    margin-bottom: 16px;
  }
  
  .section-description {
    font-size: 18px;
  }
  
  .pricing-card {
    margin: 0 20px;
    padding: 40px 32px;
  }
  
  .price {
    font-size: 3.5rem;
  }
  
  .features-list h4 {
    font-size: 1.1rem;
  }
  
  .pricing-cta {
    padding: 16px 28px;
    font-size: 16px;
  }
}

@media (max-width: 480px) {
  .pricing-card {
    margin: 0 15px;
    padding: 32px 24px;
  }
  
  .price {
    font-size: 3rem;
  }
  
  .currency {
    font-size: 1.25rem;
  }
  
  .period {
    font-size: 1rem;
  }
  
  .section-title {
    font-size: 2rem;
  }
  
  .section-description {
    font-size: 16px;
  }
  
  .feature-item {
    padding: 6px 0;
    font-size: 15px;
  }
  
  .pricing-badge {
    font-size: 12px;
    padding: 6px 16px;
  }
}