.hero {
  min-height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  background: linear-gradient(135deg, #000000 0%, #1a1a1a 50%, #000000 100%);
  padding-top: 80px;
}

.hero-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.hero-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 1;
}


.hero-video-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
  z-index: 2;
}

[data-theme="dark"] .hero-video-overlay {
  background: rgba(0, 0, 0, 0.6);
}

.hero-gradient {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(ellipse at center, rgba(0, 76, 191, 0.15) 0%, transparent 70%);
  background-size: 100% 100%;
  z-index: 3;
}

[data-theme="dark"] .hero-gradient {
  background: radial-gradient(ellipse at center, rgba(0, 76, 191, 0.25) 0%, transparent 70%);
}

.hero-particles {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 4;
}

.particle {
  position: absolute;
  width: 4px;
  height: 4px;
  background: var(--color-white);
  border-radius: 50%;
  opacity: 0.8;
  filter: blur(0.5px);
  box-shadow: 0 0 6px rgba(255, 255, 255, 0.5);
}

.hero-content {
  text-align: center;
  max-width: 800px;
  z-index: 10;
  position: relative;
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(10px);
  border-radius: 24px;
  padding: 60px 40px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  margin: 0 auto;
}

[data-theme="dark"] .hero-content {
  background: rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.hero-title {
  font-size: clamp(3rem, 6vw, 5rem);
  font-weight: 800;
  line-height: 1.1;
  margin-bottom: 24px;
  color: #FFFFFF;
  letter-spacing: -0.02em;
}

.gradient-text {
  background: linear-gradient(135deg, var(--color-accent), #0066ff, #00a3ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  position: relative;
}

.hero-description {
  font-size: 22px;
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 48px;
  line-height: 1.6;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.hero-actions {
  display: flex;
  gap: 20px;
  justify-content: center;
  align-items: center;
  margin-bottom: 60px;
  flex-wrap: wrap;
}

.hero-cta {
  padding: 18px 36px;
  font-size: 18px;
  font-weight: 600;
  border-radius: 16px;
  display: flex;
  align-items: center;
  gap: 8px;
  background: linear-gradient(135deg, var(--color-accent), #0066ff);
  border: none;
  color: white;
  box-shadow: 0 8px 32px rgba(0, 76, 191, 0.3);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.hero-secondary {
  padding: 18px 36px;
  font-size: 18px;
  font-weight: 600;
  border-radius: 16px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  background: transparent;
  color: #FFFFFF;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.hero-secondary:hover {
  border-color: var(--color-accent);
  background: rgba(0, 76, 191, 0.2);
  color: #FFFFFF;
}

.hero-stats {
  display: flex;
  justify-content: center;
  gap: 60px;
  margin-top: 40px;
}

.stat {
  text-align: center;
}

.stat-number {
  display: block;
  font-size: 2.5rem;
  font-weight: 800;
  color: var(--color-accent);
  line-height: 1;
  margin-bottom: 8px;
}

.stat-label {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

@media (max-width: 768px) {
  .hero {
    padding-top: 60px;
  }
  
  .hero-content {
    padding: 40px 30px;
    margin: 60px 20px 0 20px;
  }
  
  .hero-title {
    font-size: 3rem;
    margin-bottom: 20px;
  }
  
  .hero-description {
    font-size: 18px;
    margin-bottom: 40px;
  }
  
  .hero-actions {
    flex-direction: column;
    gap: 16px;
    margin-bottom: 50px;
  }
  
  .hero-cta, .hero-secondary {
    padding: 16px 32px;
    font-size: 16px;
    width: 100%;
    max-width: 280px;
  }
  
  .hero-stats {
    gap: 40px;
    flex-wrap: wrap;
  }
  
  .stat-number {
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  .hero-content {
    padding: 30px 20px;
    margin: 80px 10px 0 10px;
  }
  
  .hero-title {
    font-size: 2.5rem;
  }
  
  .hero-description {
    font-size: 16px;
  }
  
  .hero-stats {
    gap: 30px;
  }
  
  .stat-number {
    font-size: 1.8rem;
  }
  
  .stat-label {
    font-size: 12px;
  }
}